import noData from '@/components/noData'
import back from '@/components/back'
export default {
    components:{
        noData,
        back,
    },
    data(){
        return{
			user: this.$store.state.userInfo,
			Patient: this.$store.state.patient,
			page: 1,
			haveMore: true,
			search:'',
			list:[],
			Specialist:[],
			SubjectBuyRecordId:0,
			SpecialistHosGroupId:0,
			HosGroupName:'',
			SpNum:0,
			SpeciaFlag:false,
			show:false,
			CState:'circle',
			tDrId:0,
			tDrName:'',
			lBtn:false,
			totalM:0,
        }
    },
    created(){
		this.$bridge.callhandler('getSubjectBuyRecordId','',(data) => {
			this.SubjectBuyRecordId = data
		    responseCallback('接收到方法')
		})

		this.$post('MDTAPI/MDT/QuerySpecialistHosGroupPage',{
			PageIndex:this.page,
			PageSize:100,
			DrId:this.user.Data.User.DoctorId,
			HosGroupName:''
		}).then(o => {
            if(o.ReturnData && o.ReturnData.length){
                this.Specialist = o.ReturnData
                this.SpecialistHosGroupId = o.ReturnData[0].SpecialistHosGroupId
                console.log(this.SpecialistHosGroupId)
                this.HosGroupName = o.ReturnData[0].HosGroupName
                this.getMallMerchantInfoPage(false,o.ReturnData[0].SpecialistHosGroupId)
            }else{
                this.show = true
			}


		})

	},
    mounted(){
		this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
        onConfirm(){
            this.show =false
            const data = '123'
            this.$bridge.callhandler('Back', data)
        },
		cancelSpecialist(){
			this.SpeciaFlag = false
		},
		openSpecialist(){
			this.SpeciaFlag = true
		},
		getMallMerchantInfoPage(h,SpecialistHosGroupId){
			if(this.haveMore){
				this.$post('MDTAPI/MDT/QuerySpecialistHosMemberPage',{
					PageIndex:this.page,
					PageSize:6,
					SpecialistHosGroupId,
					SubjectType:2, //诊疗类型(2-分诊，3-会诊)
					DrName:this.search,
				}).then(o => {
					if (!o.ReturnData || o.ReturnData.length < 6){
						this.haveMore = false
					}
					else {
						this.haveMore = true
					}
					if (h){
						o.ReturnData.forEach(v => {
							v.CState = 'circle'
							this.list.push(v)
						})
					}
					else {
						o.ReturnData.forEach(v => {
							v.CState = 'circle'
						})
						this.list = o.ReturnData
					}

				})
			}
		},
		searchFor(){
			this.page = 1
			this.haveMore = true
			this.getMallMerchantInfoPage(false,this.SpecialistHosGroupId)
		},
		clickspecialist(item,idx2){
			this.SpNum = idx2
			this.page = 1
			this.haveMore = true
            this.SpecialistHosGroupId = item.SpecialistHosGroupId,
            this.HosGroupName = item.HosGroupName
			this.getMallMerchantInfoPage(false,item.SpecialistHosGroupId)
			this.SpeciaFlag = false
		},
		TrigeBtn(i,idx,num){
			this.lBtn = true
			if(num){
				this.tDrId = i.DrId
				this.tDrName = i.DrName
				this.CState = 'circle'
				this.list.forEach(v => {
					v.CState = 'circle'
				})
				i.CState = 'success-circle'
				this.totalM = i.TrialFee
			}else{
				this.tDrId = 0
				this.tDrName = ''
				this.list.forEach(v => {
					v.CState = 'circle'
				})
				this.CState = 'success-circle'
				this.totalM = 0
			}
		},
		ClickRecord(){
			let l = false
			this.list.forEach(v => {
				if(v.CState.indexOf('success-circle') != -1){
					l = true  //找到了 true
				}
			})
			if(this.CState == 'circle'){
				if(!l){
					return this.$toast('请选择服务或医生')
				}
			}
			this.$post('MDTAPI/MDT/SendMDTTriageMessag',{
				SubjectBuyRecordId:this.SubjectBuyRecordId,//多学科购买记录Id(多学科咨询购买记录Id)
				PatientId: this.Patient.PatientId,
				PatientName: this.Patient.PatientName,
				DoctorLevelId: 0,//医生等级id
				DoctorLevelName:'',//医生等级名称
				DrId:this.tDrId,//医生Id(分诊医生)-大于0是指定医生的分诊,或者是抢诊
				DrName:this.tDrName,
				SpecialistHosGroupId:this.SpecialistHosGroupId,
				HosGroupName:this.HosGroupName,
				SourceDrId:this.user.Data.User.DoctorId,
				SourceDrName:this.user.Data.User.DoctorName,
			}).then(o => {
                if(o){
                    const data = '123'
					this.$bridge.callhandler('Back', data)
                }
			})
		},
        getMore(){
			const el = this.$refs.el
			let timer2 = null
			window.onscroll = () => {
				clearTimeout(timer2)
				timer2 = setTimeout(() => {
					const top = document.documentElement.scrollTop ||
							window.pageYOffset ||
							document.body.scrollTop,
							h = el.scrollHeight,
							wh = window.innerHeight
					if(h - top - wh < 100 && this.haveMore) {
						this.page += 1
						this.getMallMerchantInfoPage(true,this.SpecialistHosGroupId)
					}
				}, 100)
			}
        },
    }
}



